import { useEffect } from 'react';
import { ReactComponent as Indent } from '../../../img/icons/indent.svg';
import SearchBlock from './components/SearchBlock/SearchBlock';
import { useAppSelector } from '../../../store';
import { addClassMenu, showMenu } from '../../../jsFunction';

import './style/Tuner.scss';

export const Tuner: React.FC = () => {
  const { oneType } = useAppSelector((state) => state.device);

  const name = ('name' in oneType && oneType.name) || '';

  useEffect(() => {
    showMenu();
  }, []);

  return (
    <div className='container'>
      <div className='product__tuner tuner'>
        <div className='tuner__container'>
          <div className='tuner__nameCategory'>
            <h2>{name}</h2>
          </div>
          <BlockTuner />
        </div>
      </div>
    </div>
  );
};

export const BlockTuner: React.FC = () => {
  return (
    <div className='tuner__sort sort'>
      <div className='sort__search'>
        <SearchBlock />
      </div>

      <div onClick={addClassMenu} className='sort__filter'>
        <Indent />
      </div>
    </div>
  );
};
