import { useSearchParams } from 'react-router-dom';
import { selectedMine } from '../selectedMine';

import './style/SortBlock.scss';

export const SortProduct: React.FC = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const filter = searchParams.get('filter') || 'low';

  const checkSorting = (e: React.MouseEvent<HTMLElement>) => {
    selectedMine(e, 'li');

    const element = (e.target as HTMLElement).closest('li') as HTMLElement;

    const target = element.dataset.type;

    if (target?.includes(searchParams.get('filter') || 'null')) {
      searchParams.delete('filter');
    } else {
      target && searchParams.set('filter', target);
    }

    setSearchParams(searchParams);
  };

  return (
    <>
      <li
        data-type='low'
        data-filter='Price: Low - High'
        onClick={checkSorting}
        className={filter.includes('low') ? 'add' : ''}
      >
        <div className='wemersion__ok'></div>
        <div className='wemersion__text'>
          <p className='mini'>Price: Low - High</p>
        </div>
      </li>
      <li
        data-type='high'
        data-filter='Price: High - Low'
        onClick={checkSorting}
        className={filter.includes('hight') ? 'add' : ''}
      >
        <div className='wemersion__ok'></div>
        <div className='wemersion__text'>
          <p className='mini'>Price: High - Low</p>
        </div>
      </li>
    </>
  );
};

export const handlerSort = (target: string | undefined, arr: any) => {
  if (target == 'low') {
    arr.sort((a, b) => +a.price.replace('$', '') - +b.price.replace('$', ''));
  } else if (target == 'high') {
    arr.sort((a, b) => +b.price.replace('$', '') - +a.price.replace('$', ''));
  }
  return arr;
};
