import React from 'react';
import { useSearchParams } from 'react-router-dom';
import { filter, useNavigateSearch } from '../Brends/Brends';
import { selectedMineAll } from '../selectedMine';
import {
  TypesConfig,
  useAppDispatch,
  useAppSelector,
} from '../../../../../store';
import {
  pageInitial,
  setColors,
} from '../../../../../store/goodsSlices/devicesAPI/deviceSlice';
import { ProductT } from '../../../../../store/goodsSlices/goods.config';

export const Colors = () => {
  const [searchParams] = useSearchParams();
  const { colors, oneColor, oneType, goodsAll } = useAppSelector(
    (state) => state.device
  );
  const dispatch = useAppDispatch();

  const params = useNavigateSearch();

  const selected = (e: React.MouseEvent<HTMLElement>, item: TypesConfig) => {
    selectedMineAll(e, 'li');
    let color: TypesConfig[] = oneColor.reduce(
      (prev: TypesConfig[], itemtwo) => {
        if (itemtwo.name != 'ALL' && itemtwo.name != item.name) {
          prev.push(itemtwo);
        }
        return prev;
      },
      []
    );

    color = filter(color, item);

    params('color', item);

    dispatch(pageInitial());
    dispatch(setColors({ colors: color }));
  };

  const sort = () => {
    const map = new Map();
    const colorsNew = colors;

    map.set(
      'All',
      <li
        key='All'
        className={searchParams.toString().includes('color') ? '' : 'add'}
        data-type='All'
        onClick={(e) => selected(e, { id: 0, name: 'ALL' })}
      >
        <div className='wemersion__ok'></div>
        <div className='wemersion__text'>
          <p className='mini'>All Colors</p>
        </div>
      </li>
    );
    if (Array.isArray(colorsNew)) {
      'id' in oneType &&
        colorsNew.map((item: TypesConfig, index) => {
          if (item.name != 'colorless') {
            if (
              goodsAll.filter(
                (el: ProductT) =>
                  +el.typeId == oneType.id && +el.colorId == item.id
              ).length != 0
            ) {
              map.set(
                item.id,
                <li
                  className={
                    searchParams.get('color' + item.id) == item.id.toString()
                      ? 'add'
                      : ''
                  }
                  data-type={item.name}
                  onClick={(e) => selected(e, item)}
                  key={index}
                >
                  <div className='wemersion__ok'></div>
                  <div className='wemersion__text'>
                    <p className='mini'>
                      {item.name
                        .split('')
                        .map((item2: string, index2: number) => {
                          return index2 == 0
                            ? item2.toUpperCase()
                            : item2.toLowerCase();
                        })}
                    </p>
                  </div>
                  <div
                    className='wemersion__color'
                    style={{ background: `${item.color}` }}
                  ></div>
                </li>
              );
            }
          }
        });
    }
    return Array.from(map.values());
  };

  return <>{sort()}</>;
};
