import React, { useEffect } from 'react';
import ProductTable from './components/ProductTable/ProductTable';
import { useLocation } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../../store';
import {
  initialStateHTTP,
  productItogHTTP,
} from '../../../store/goodsSlices/devicesAPI/deviceAPI';
import { LoadingWin } from '../../../components';
import { renderSortMenu } from '../../../jsFunction/addClassSort&Search';
import { changeLimitDevice } from '../../../store/goodsSlices/devicesAPI/deviceSlice';
import { AsideBlockTurn } from '../Tuner/AsideBlockTurn';
import { Tuner } from '../Tuner/Tuner';
import { SortProduct } from '../Tuner/components/SortBlock/SortProduct';
import GoodsRow from '../GoodsRow/GoodsRow';

import './style/Goods.scss';

export const Goods: React.FC = () => {
  const dispatch = useAppDispatch();
  const { isLoading } = useAppSelector((state) => state.device);
  const { search } = useLocation();

  useEffect(function () {
    dispatch(initialStateHTTP());
  }, []);

  useEffect(() => {
    if (!isLoading) dispatch(productItogHTTP());
  }, [search]);

  const changeLimit = function () {
    const width = window.innerWidth;
    if (!isLoading && width <= 1420) {
      dispatch(changeLimitDevice({ limit: '6' }));
    } else if (!isLoading) {
      dispatch(changeLimitDevice({ limit: '8' }));
    }
  };

  useEffect(() => {
    window.addEventListener('resize', changeLimit);
    window.addEventListener('click', renderSortMenu);
    return function cleanup() {
      window.removeEventListener('resize', changeLimit);
      window.removeEventListener('click', renderSortMenu);
    };
  }, []);

  return (
    <div className='goods indent_last'>
      <Tuner />
      <GoodsRow>
        <AsideBlockTurn>
          <SortProduct />
        </AsideBlockTurn>
        <LoadingWin isLoading={isLoading}>
          <ProductTable />
        </LoadingWin>
      </GoodsRow>
    </div>
  );
};
