import React from 'react';
import { useLocation, useSearchParams } from 'react-router-dom';
import { selectedMine } from '../selectedMine';
import { TypesConfig, useAppSelector } from '../../../../../store';

export const ProductCategoryRow: React.FC = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const router = useLocation().search;
  const { types, oneType } = useAppSelector((state) => state.device);

  const selected = async (
    e: React.MouseEvent<HTMLElement>,
    item: TypesConfig
  ) => {
    if (!(+(searchParams.get('type') as string) == +item.id)) {
      selectedMine(e, 'li');

      searchParams.set('type', `${item.id}`);
      searchParams.set('page', `1`);
      const brand = router.match(/brand\d{1,2}/g);
      const color = router.match(/color\d{1,2}/g);

      searchParams.delete('l');
      searchParams.delete('t');

      deleteSearch(brand);
      deleteSearch(color);

      setSearchParams(searchParams);
    }
  };

  function deleteSearch(element: RegExpMatchArray | null) {
    if (Array.isArray(element)) {
      element.map((el) => {
        searchParams.delete(el);
      });
    }
  }

  const sort = () => {
    const map = new Map();
    const typesNew = types;

    if (Array.isArray(typesNew) && 'id' in oneType) {
      typesNew.map((item, index) => {
        map.set(
          item.id,
          <li
            data-type={item.name}
            className={oneType.id == item.id ? 'add' : ''}
            onClick={(e) => selected(e, item)}
            key={index}
          >
            <div className='wemersion__ok'></div>
            <div className='wemersion__text'>
              <p className='mini'>
                {item.name.split('').map((item2: string, index2: number) => {
                  return index2 == 0
                    ? item2.toUpperCase()
                    : item2.toLowerCase();
                })}
              </p>
            </div>
          </li>
        );
      });
    }
    return Array.from(map.values());
  };

  return <>{sort()}</>;
};
