import { useMemo, useEffect, useState } from 'react';
import { useLocation, useSearchParams } from 'react-router-dom';
import { Brends, useNavigateSearch } from './components/Brends/Brends';
import { ProductCategoryRow } from './components/Category/Category';
import { ReactComponent as X } from '../../../img/icons/x.svg';
import WEmersion from './components/WEmersion/WEmersion';
import { Colors } from './components/Color/Color';
import { SortPrice } from './components/SortBlock/SortPrice';
import { TypesConfig, useAppDispatch, useAppSelector } from '../../../store';
import { $ } from '../../../components';
import {
  pageInitial,
  setBrands,
  setColors,
} from '../../../store/goodsSlices/devicesAPI/deviceSlice';
import { productItogHTTP } from '../../../store/goodsSlices/devicesAPI/deviceAPI';

import './style/Tuner.scss';

type AsignBlockT = {
  children: JSX.Element;
};

export const AsideBlockTurn: React.FC<AsignBlockT> = ({ children }) => {
  const { oneType, brands, colors } = useAppSelector((state) => state.device);
  const [searchParams] = useSearchParams();
  const router = useLocation().search;
  const target = ($('.filter  ul li.add').take() as HTMLElement)?.dataset
    .filter;

  function checkElement(arr, item) {
    const regexp = `${item}\\d{1,2}`;

    const search = router.match(
      new RegExp(regexp, 'g')
    ) as RegExpMatchArray | null;

    if (search && search.length > 1) {
      return 'Several';
    } else if (search && search.length == 1 && arr.length > 0) {
      return arr
        .reduce((prev, el) => {
          if (el.id == searchParams.get(search[0])) {
            prev.push(el);
          }
          return prev;
        }, [])[0]
        .name.split('')
        .map((item, index) => {
          return index == 0 ? item.toUpperCase() : item;
        })
        .join('');
    } else {
      return 'ALL';
    }
  }

  return (
    <div className='sort__aside'>
      <div className='sort__wrap'>
        <div className='sort__blockone'>
          <div className='sort__block filter'>
            <div className='sort__title'>
              <h3>Filter</h3>
            </div>
            <WEmersion name={target || 'Not chosen'}>{children}</WEmersion>
          </div>

          <div className='sort__block'>
            <div className='sort__title'>
              <h3>Category</h3>
            </div>
            <WEmersion name={('name' in oneType && oneType.name) || ''}>
              <ProductCategoryRow />
            </WEmersion>
          </div>
          <div className='sort__block'>
            <div className='sort__title'>
              <h3>Brand</h3>
            </div>
            <WEmersion name={checkElement(brands, 'brand')}>
              <Brends />
            </WEmersion>
          </div>
          <div className='sort__block'>
            <div className='sort__title'>
              <h3>Color</h3>
            </div>
            <WEmersion name={checkElement(colors, 'color')}>
              <Colors />
            </WEmersion>
          </div>
          <div className='sort__block'>
            <div className='sort__title'>
              <h3>Price</h3>
            </div>
            <WEmersion name={'Price range'}>
              <SortPrice />
            </WEmersion>
          </div>
          <div className='sort__block'>
            <SaleAsideBlock />
          </div>
        </div>
      </div>
      <TurnBlockAddition />
    </div>
  );
};

export const SaleAsideBlock: React.FC = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const dispatch = useAppDispatch();

  const selected = (e: React.MouseEvent<HTMLElement>) => {
    const target = e.target as HTMLElement;

    $(target.closest('li') as HTMLElement).toggle('add');
    if (searchParams.toString() != '') {
      if (!searchParams.has(`sale`)) {
        searchParams.set('sale', 'true');
      } else {
        searchParams.delete(`sale`);
      }
      setSearchParams(searchParams);
    } else {
      searchParams.set('sale', 'true');
      setSearchParams(searchParams);
    }
    dispatch(pageInitial());
    dispatch(productItogHTTP());
  };

  return (
    <div className='saleasideblock'>
      <ul>
        <li
          className={searchParams.get('sale') ? 'add' : ''}
          onClick={(e) => selected(e)}
        >
          <div className='wemersion__ok'></div>
          <div className='wemersion__text'>
            <p className='mini'>Discount goods</p>
          </div>
        </li>
      </ul>
    </div>
  );
};

export const TurnBlockAddition: React.FC = () => {
  const { brands, colors, isLoading } = useAppSelector((state) => state.device);
  const dispatch = useAppDispatch();
  const query = useQuery();
  const [arr, setArr] = useState<TypesConfig[]>([]);
  const params = useNavigateSearch();
  const { search } = useLocation();

  const handlerDelete = (item: TypesConfig) => {
    const items: TypesConfig[] = [];
    const color: TypesConfig[] = [];
    params(item.dop, item);
    for (const [key, value] of query.entries()) {
      if (key.startsWith('brand')) {
        brands.map((item) => {
          if (item.id == +value) items.push(item);
        });
      } else if (key.startsWith('color')) {
        colors.map((item) => {
          const obj = { ...item };
          obj.dop = 'color';
          if (item.id == +value) color.push(item);
        });
      }
    }
    dispatch(setColors({ colors: color }));
    dispatch(setBrands({ brands: items }));

    dispatch(pageInitial());
    dispatch(productItogHTTP());
  };

  const isTrust = () => {
    const items: TypesConfig[] = [];
    if (!isLoading) {
      for (const [key, value] of query.entries()) {
        if (key.startsWith('brand')) {
          brands.map((item) => {
            const obj = { ...item };
            obj.dop = 'brand';
            if (item.id == +value) items.push(obj);
          });
        } else if (key.startsWith('color')) {
          colors.map((item) => {
            const obj = { ...item };
            obj.dop = 'color';
            if (item.id == +value) items.push(obj);
          });
        }
      }
    }

    setArr(items);
  };

  //   ! =====================

  useEffect(() => {
    isTrust();
  }, [search, brands, colors]);

  return (
    <div className='sort__add'>
      <div className='sort__additems'>
        {arr.length > 0 &&
          arr.map((item: TypesConfig) => {
            return (
              <div key={item.id + '' + item.name} className='sort__item'>
                <p className='mini '>
                  {item.name
                    .split('')
                    .map((item, index) => {
                      if (index == 0) return item.toUpperCase();
                      return item;
                    })
                    .join('')}
                </p>
                <div
                  onClick={() => handlerDelete(item)}
                  className='sort__close'
                >
                  <X />
                </div>
              </div>
            );
          })}
      </div>
    </div>
  );
};

export function useQuery() {
  const { search } = useLocation();

  return useMemo(() => new URLSearchParams(search), [search]);
}
