import { useEffect, useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../../../../store';
import { pageInitial } from '../../../../../store/goodsSlices/devicesAPI/deviceSlice';
import { productItogHTTP } from '../../../../../store/goodsSlices/devicesAPI/deviceAPI';
import { $ } from '../../../../../components';

import './style/SortBlock.scss';

export const SortPrice: React.FC = () => {
  const [low, setLow] = useState('0');
  const [tall, setTall] = useState('9999');
  const [maxThumb, setMaxThumb] = useState(9999);
  const { sum, goodsSort } = useAppSelector((state) => state.device);
  const dispatch = useAppDispatch();
  const [searchParams, setSearchParams] = useSearchParams();

  const change = () => {
    if (sum > 0) {
      setMaxThumb(Math.ceil(sum));
      setTall(Math.ceil(sum).toString());
    }
    if (searchParams.has('l')) {
      setLow(searchParams.get('l') as string);
    }
    if (searchParams.has('t')) {
      setTall(searchParams.get('t') as string);
    }
  };

  useEffect(() => {
    if (sum != 0 && sum != +setMaxThumb) change();
  }, [goodsSort]);

  useEffect(() => {
    if (+tall != 9999 && maxThumb != 9999) fill();
  }, [maxThumb, tall]);

  const handlerChange = (e) => {
    const target = e.target.closest('input[type="text"]');
    const sum = e.target.value.replace(/[^\d]*/g, '');

    if (target.dataset.thumb == 'l') {
      setLow(sum);
    } else if (target.dataset.thumb == 't') {
      if (sum <= maxThumb) setTall(sum);
      else setTall(maxThumb.toString());
    }

    searchParams.set(target.dataset.thumb, e.target.value);
    setSearchParams(searchParams);
    changePage();
  };

  function changePage() {
    searchParams.delete(`sale`);
    setSearchParams(searchParams);
    dispatch(pageInitial());
    dispatch(productItogHTTP());
  }

  function handlerThumbInout(e) {
    const target = e.target.closest('input[type="range"]');

    if (target.dataset.thumb == 't') {
      if (+e.target.value - 5 > +low) {
        setTall(e.target.value);
      }
    } else if (target.dataset.thumb == 'l') {
      if (+e.target.value - 5 < +tall) {
        setLow(e.target.value);
      }
    }

    fill();

    document.onmouseup = function () {
      document.onmouseup = function () {
        return false;
      };
      searchParams.set(target.dataset.thumb, e.target.value);
      setSearchParams(searchParams);
      changePage();
    };
  }

  function fill() {
    const pointone = (+low / maxThumb) * 100;
    const pointtwo = (+tall / maxThumb) * 100;

    (
      $('.sortprice__groud').take() as HTMLElement
    ).style.background = `linear-gradient(to right, #e0e0e0 ${pointone}%, #f7ca18 ${pointone}%, #f7ca18 ${pointtwo}%, #e0e0e0 ${pointtwo}%`;
  }

  return (
    <li data-type='numberLager ' className='sortprice'>
      <div className='sortprice__price'>
        <div className='sortprice__low sortprice__input'>
          <input
            data-thumb='l'
            type='text'
            value={low}
            onChange={handlerChange}
            maxLength={4}
          />
        </div>

        <div className='sortprice__tall sortprice__input'>
          <input
            data-thumb='t'
            type='text'
            value={tall}
            onChange={handlerChange}
            maxLength={4}
          />
        </div>
      </div>

      <div className='sortprice__body'>
        <div className='sortprice__groud'></div>

        <input
          data-thumb='l'
          type='range'
          min={0}
          max={maxThumb}
          value={low}
          onChange={handlerThumbInout}
        />
        <input
          data-thumb='t'
          type='range'
          min={0}
          max={maxThumb}
          value={tall}
          onChange={handlerThumbInout}
        />
      </div>
    </li>
  );
};
